import React from 'react';
import Persona from './personafrom.js'
import './laralala2.css';

let idsvariable = [0,]

/*let idsvariable = [0,1,2,3,8,456,9]*/

class Personas extends React.Component {
    
    state={
        ids:idsvariable
    }

    enClick=()=>{
        let idnueva = idsvariable.push( idsvariable[idsvariable.length-1]+1 )
        //alert('en click! ' + ids)

        this.setState({
            ids: [...this.state.ids, idnueva]
          })
    }

    eliminarPersona=(id)=>{
        console.log('eliminar:', id)
        //hace una nueva lista pero sin la persona eliminada
        const personasnuevas = this.state.ids.filter(idlist => idlist !== id)
        console.log('nuevalista:', personasnuevas)

        this.setState({
            ids: personasnuevas
          })

    }

    enEnvio=(evento)=>{
        evento.preventDefault();
    }

    render() { 
        return <div className="container otrofondo">
            <div className="row col12 align-items-center">
                <form onSubmit={this.enEnvio}>
                    {this.state.ids.map(id => <Persona key = {id} id={id} eliminarPersona = {this.eliminarPersona}/>)}
                    <input className="btn btn-outline-dark" type='submit'></input>
                    <button type="button" className="btn btn-success float-end" id='boton' name='botonpersonas' onClick = {this.enClick}>AÃ±adir</button>
                </form>
                
            </div>
        </div>
    }
}

export default Personas;