import React from 'react';
import './larala.css';


class Persona extends React.Component {

    render() { 
        return <div className="contenedor container shadow-lg p-3 mb-5 bg-body rounded">
            persona{this.props.id} :
            <button className="btn btn-outline-danger float-end" id='botoneliminar' name='botoneliminar' onClick = {this.props.eliminarPersona.bind(this, this.props.id)}>X</button>
            <div className="form-floating mb-3">
            <input className="form-control" id={this.props.id} name ={'nombre' + this.props.id} type='text' placeholder='nombre' required></input>
            <label htmlFor={this.props.id}>nombre</label>
            </div>

            <div className="form-floating mb-3">
            <input className="form-control" id={this.props.id} name ={'apellido' + this.props.id} type='text' placeholder='apellido' required></input>
            <label htmlFor={this.props.id}>apellido</label>
            </div>

        </div>
    }
}

/*
const personacontainer = {
    border: '2px solid #000',
    background: 'red',
}
*/
export default Persona;